.btnAddRemove {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    border-radius: 100px;
    padding: 0.3rem;
    cursor: pointer;
    font-size: 0.6rem;
    background-color: #000;
    color: #fff;
}

.btnAddRemove-negative,
.btnAddRemove-positive {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
}